<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right">
      <button class="btn btn-primary mt-4" @click="openEditCouponModal(true)">
        
      </button>
    </div>
    <!-- CouponContent -->
    <table class="table mt-4">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, key) in coupons" :key="key">
          <td>{{ item.title }}</td>
          <td>{{ item.percent }}%</td>
          <td>{{ item.due_date | DateFilter }}</td>
          <td>
            <span v-if="item.is_enabled === 1" class="text-success"></span>
            <span v-else class="text-muted"></span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm"
              @click="openEditCouponModal(false, item)"
            ></button>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm"
              @click="openDelCouponModal(item)"
            ></button>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- EditCouponModal -->
    <div class="modal fade" id="couponModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark">
            <h5 class="modal-title text-white" id="exampleModalLabel"> / </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true" class="text-white">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label for="title"></label>
              <input type="text" class="form-control" id="title" v-model="tempCoupon.title"
                placeholder="">
            </div>
            <div class="form-group">
              <label for="coupon_code"></label>
              <input type="text" class="form-control" id="coupon_code" v-model="tempCoupon.code"
                placeholder="">
            </div>
            <div class="form-group">
              <label for="due_date"></label>
              <input type="date" class="form-control" id="due_date"
                v-model="due_date">
            </div>
            <div class="form-group">
              <label for="price"></label>
              <input type="number" class="form-control" id="price"
                v-model="tempCoupon.percent" placeholder="">
            </div>
            <div class="form-group">
              <div class="form-check">
                <input class="form-check-input" type="checkbox"
                  :true-value="1"
                  :false-value="0"
                  v-model="tempCoupon.is_enabled" id="is_enabled">
                <label class="form-check-label" for="is_enabled">
                  
                </label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary"
              @click="updateCoupon"></button>
          </div>
        </div>
      </div>
    </div>
    <!-- DelCouponModal -->
    <div
      class="modal fade"
      id="delCouponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="DelModalLable"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger">
            <h5 class="modal-title text-white" id="DelModalLable">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true" class="text-white">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempCoupon.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger"
            @click.prevent="removeCoupon"></button>
          </div>
        </div>
      </div>
    </div>
    <!-- Pagination -->
    <Pagination
    :pageData="pages"
    @emitGetPage="getCoupons"/>
  </div>
</template>
<script>
/* eslint-disable */
import $ from 'jquery';
import Pagination from '../Pagination';

export default {
  data() {
    return {
      coupons: {},
      tempCoupon: {},
      due_date: new Date(),
      isNew: false,
      pages: {},
      isLoading: false,
    };
  },
  watch: {
    due_date() {
      //timestamp
      const vm = this;
      const timestamp = Math.floor(new Date(vm.due_date) / 1000);
      vm.tempCoupon.due_date = timestamp;
    },
  },
  methods: {
    // 
    openEditCouponModal(judge, item) {
      const vm = this;
      $('#couponModal').modal('show');
      vm.isNew = judge;
      if (vm.isNew) {
        vm.tempCoupon = {};
      } else {
        // es6 assign
        vm.tempCoupon = { ...item };
        //timestamp"T"ISO
        const dateAndTime = new Date(vm.tempCoupon.due_date * 1000).toISOString().split('T');
        vm.due_date = dateAndTime[0];
      }
    },
    getCoupons() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons`;
      vm.isLoading = true;
      this.$http.get(url, vm.tempProduct).then((response) => {
        vm.pages = response.data.pagination;
        vm.coupons = response.data.coupons;
        vm.isLoading = false;
      });
    },
    updateCoupon() {
      const vm = this;
      if (vm.isNew) {
        // post
        const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
        this.$http.post(url, { data: vm.tempCoupon }).then(() => {
          $('#couponModal').modal('hide');
          this.getCoupons();
        });
      } else {
        // put
        const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
        vm.due_date = new Date(vm.tempCoupon.due_date * 1000);
        this.$http.put(url, { data: vm.tempCoupon }).then(() => {
          $('#couponModal').modal('hide');
          this.getCoupons();
        });
      }
    },
    openDelCouponModal(item) {
      const vm = this;
      vm.tempCoupon = { ...item };
      $('#delCouponModal').modal('show');
    },
    removeCoupon() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
      vm.$http.delete(url).then((response) => {
        if (response.data.success) {
          vm.getCoupons();
          $('#delCouponModal').modal('hide');
          vm.$bus.$emit('alert', '', 'success');
        } else {
          vm.getCoupons();
          $('#delCouponModal').modal('hide');
          vm.$bus.$emit('alert', '', 'danger');
        }
      });
    },
  },
  created() {
    this.getCoupons();
  },
  components: {
    Pagination,
  },
};
</script>



